/*=============================
            REGISTER
===============================*/

.rl_container {
    text-align: center;
}

.rl_container .form_element {
    margin-top: 20px;
}


.rl_container h2 {
    font-size: 33px;
    font-weight: 300;
    color: #02bcd4;
}

.rl_container input {
    font-size: 20px;
    border-radius: 3px;
    border: 1px solid #ebebeb;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 8px 8px;
    background: #f7f7f7;
    font-weight: 300;
    width: 260px;
}
.rl_container.article input {
    width: 80%;
}

.rl_container.article textarea {
    background: #f7f7f7;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    width: 80%;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 20px;
    font-size: 14px;
}
.rl_container.article select {
    width: 80%;
    background: #f7f7f7;
    font-size: 20px;
    border: 1px solid #e1e1e1;
}

.rl_container button {
    font-size: 20px;
    padding: 10px 52px;
    background: #02bcd4;
    color: #fff;
    font-weight: 300;
    border-radius: 3px;
    border: none;
    margin-top: 20px;
    width: 260px;
    cursor: pointer;
}
.rl_container.article button {
    width: 80%;
    cursor: pointer;
}

.rl_container .conf_link {
    color: #8BC34A;
    font-weight: 600;
    margin-top: 20px;
}

.rl_container .error {
    color: #FF5722;
    margin-top: 10px;
    font-weight: 600;
}

.rl_container .success a {
    color: #FF5722;
    font-size: 20px;
}

.rl_container .delete_post {
    text-align: center;
    margin-top: 20px;
}

.rl_container .delete_post .button {
    font-size: 20px;
    padding: 10px 52px;
    background: #E91E63;
    color: #fff;
    font-weight: 300;
    border-radius: 3px;
    border: none;
    width: 80%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    cursor: pointer;
}


